import React, { Component } from 'react';
import { HashRouter as Router, Route, Link } from "react-router-dom";
import createHistory from 'history/createBrowserHistory'
import Login from './routes/Login.js'
import logo from './logo.svg';
import 'typeface-roboto';
import './App.css';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles'
import purple from '@material-ui/core/colors/purple';
import ScrollToTop from './routes/functionals/ScrollToTop'
import SectionSelector from './routes/SectionSelector.js'
import Homepage from './routes/Homepage.js'
import Quiz from './routes/Quiz'
import Formular from './routes/Formular'
import Referendum1 from './routes/Referendum1'
import Referendum2 from './routes/Referendum2'
import Faq from './routes/Faq'

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#002a59',
    },
    secondary: {
      main: '#002a59',
    },
  },
});
class App extends Component {
  render() {
    return (
      <Router>
        <ScrollToTop>
          <MuiThemeProvider theme = { theme }>
            <div className='page_center'>
              <Route path="/" exact component={Login} />
              <Route path="/section_selector" component={SectionSelector} />
              <Route path="/homepage" component={Homepage} />
              <Route path="/quiz" component={Quiz} />
              <Route path="/formular" component={Formular} />
              <Route path="/referendum1" component={Referendum1} />
              <Route path="/referendum2" component={Referendum2} />
              <Route path="/faq" component={Faq} />
            </div>
          </MuiThemeProvider>
        </ScrollToTop>
    </Router>
    );
  }
}

export default App;
