function formatReqStringOBJ(obj){
    var formatSendTags = '';
    var and = '';
    Object.keys(obj).map((key, i, arr) => {
            if(Object.keys(obj).length - 1 == i){
                and = '';
            }else{
                and = '&';
            }
            formatSendTags += key + "=" + obj[key] + and;
    })
    return formatSendTags;
}
class ReferendumForm extends Component {
    constructor(props){
        super(props);
        this.state = {
            user: localStorage.getItem('user') != null ? JSON.parse(localStorage.getItem('user')) : '',
            fields: {},
            errors: {
                classes: {},
                messages: {}
            },
            openSnack: false,
            snackTheme: 1,
            server_response_message: '',
            dialog_open: false,
            dialog_message: '',
            focused_fields: [],
            form_has_been_submitted: false,
            formNumber: this.props.formNumber ? this.props.formNumber : ''
        }
        this.textField = [];
        this.focusTextField = this.focusTextField.bind(this);
    }
    componentDidMount(){
        var self = this
        var allowed_fields = []
        for(var key in form_fields){
            allowed_fields.push('ref' + this.state.formNumber + '_' + (parseInt(key) + 1))
        }
        CustomAjaxRequest(global.app_url + 'observer/votes?observer_id=' + this.state.user.id + '&token=' + this.state.user.token, 'GET', '', function(r) {
            var res = JSON.parse(r);
            if(res.ok){
                if(res.section){
                    for(var key in res.section){
                        if(allowed_fields.includes(key)){
                            var updated_fields = self.state.fields
                            updated_fields[key] = res.section[key] != null ? res.section[key] : 0
                            self.setState({
                                updated_fields
                            })
                        }
                    }
                }
            }
        })
    }
    handleChange(field, e) {
        var fields = this.state.fields;
        fields[field] = e.target.value.replace(/\D/g,'');;
        
		var errors = {
			classes: {},
			messages: {}
		};
     
        this.setState({
            fields
        });
        //localStorage.setItem('formular', JSON.stringify(fields))
    }
    handleValidation(){
        var fields = this.state.fields;
        var errors = {
            classes: {},
            messages: {}
        };
        var formIsValid = true;
        var self = this;
        form_fields.map((field, index) => {
            if(field.mandatory){
                var final_field_name = 'ref' + self.state.formNumber + '_' + (index + 1);
                if (!fields[final_field_name]) {
                    formIsValid = false;
                    errors['classes'][final_field_name + '_error'] = 'custom_error';
                    errors['messages'][final_field_name] = 'Camp obligatoriu';
                    errors[final_field_name] = true;
                }
            }
        })
        this.setState({
            errors: errors
        }, function() {});
        return formIsValid;
    }
    handleSubmit(e) {
        e.preventDefault();
        if (this.handleValidation()) {
            //success //redirect to homepage
            this.setState({
                dialog_open: true
            })
        } else {
            //alert("Form has errors.")
        }
    }
    snackbarClose(){
        this.setState({openSnack: false})
    }
    onFocus(field){
        var focused_fields = this.state.focused_fields;
        focused_fields[field] = true
        this.setState({
            focused_fields
        });
    }
    onBlur(field){
        var focused_fields = this.state.focused_fields;
        focused_fields[field] = false
        this.setState({
            focused_fields
        });
    }
    focusTextField(field_name) {
        // Explicitly focus the text input using the raw DOM API
        // Note: we're accessing "current" to get the DOM node
        this.textField[field_name].current.focus();
    }
    handleCancelSave = () => {
        this.setState({
            dialog_open: false
        })
    }
    handleOkSave = () => {
        var self = this;
        var fields_params = formatReqStringOBJ(this.state.fields)
        if (this.state.form_has_been_submitted === false) {
            this.setState({form_has_been_submitted: true, dialog_open: false}, function(){
                if(Object.keys(this.state.fields).length > 0){
                    CustomAjaxRequest('observer_id=' + this.state.user.id + '&token=' + this.state.user.token + '&' + fields_params + '&nr=' + this.state.formNumber, 'POST', global.app_url + 'observer/save_ref', function(r) {
                        var res = JSON.parse(r);
                        self.setState({
                            openSnack: true,
                            form_has_been_submitted: false,
                            dialog_open: false
                        })
                        if (res.ok === false) {
                            self.setState({
                                server_response_message: res.errorLabel,
                                snackTheme: 1
                            })
                        } else {
                            localStorage.setItem('formular_referendum_' + self.state.formNumber + '_completat', 'da')
                            //localStorage.setItem('user', JSON.stringify(res))
                            self.setState({
                                server_response_message: "Datele au fost salvate.",
                                snackTheme: 2,
                                //fields: {}
                            }, function() {
                                self.props.history.push('/homepage')
                            })
                        }
                        //check for error messages and add condition
                    })
                }
            });
        }
    }
    render() {
        const { classes } = this.props;
        const { history } = this.props;
        var self = this
        
        return (
            <div className='section_selector'>
                <Header user={this.state.user} history={this.props.history} pagename={'P.V. REFERENDUM ÃŽNTREB #' + this.state.formNumber} />
                <form className={classes.container} autoComplete="off" onSubmit = { this.handleSubmit.bind(this) }>
                        {form_fields.map((field, index) => {
                            var final_field_name = 'ref' + this.state.formNumber + '_' + (index + 1);
                            return(
                                <Fade in={true} timeout={1000}>
                                    <FormControl fullWidth className='form_control'>
                                        <InputLabel htmlFor={final_field_name} className='form_control_label'><span className='mandatory_field'>{field.mandatory ? '*' : ''}</span>{(index + 1) + '. ' + field.label_text}</InputLabel>
                                        <TextField
                                            error={this.state.errors[final_field_name]}
                                            className={classes.textField + " " + this.state.errors['classes'][final_field_name + '_error'] + ' custom_field' + " " + (this.state.focused_fields[final_field_name] == true ? ' expanded_input' : '')}
                                            value={this.state.fields[final_field_name] || ''}
                                            onChange={this.handleChange.bind(this, final_field_name)}
                                            fullWidth
                                            autoCapitalize='none'
                                            type={field.field_type}
                                            onFocus={this.onFocus.bind(this, final_field_name)}
                                            onBlur={this.onBlur.bind(this, final_field_name)}
                                        />
                                    </FormControl>
                                </Fade>
                            )
                        })}
                        
                        <Grid container spacing={0}>
                            <Grid item xs={12} className='multiple_buttons'>
                                <Button type='submit' className='custom_btn back_btn link_btn section_save_button' to=''>SALVEAZÄ‚</Button>
                            </Grid>
                        </Grid>
                </form>
                <p className='small_disclaimer'><strong>*)</strong> NumÄƒrul participanÈ›ilor trebuie sÄƒ fie egal cu suma ce rezultÄƒ din adiÈ›ionarea cifrelor de la pct. 5, 6 È™i 7.</p>
                <Snackbar open = { this.state.openSnack }
                        anchorOrigin = {
                            {
                                vertical: 'bottom',
                                horizontal: 'left',
                            }
                        }
                        maxSnack = { 2 }
                        autoHideDuration = { 3000 }
                        onClose = { this.snackbarClose.bind(this) }
                        message = { <span className='server_response_message'> { this.state.server_response_message } </span>}
                            ContentProps = {
                                {
                                    classes: {
                                        root: this.state.snackTheme === 1 ? classes.snackError : classes.snackSuccess
                                    }
                                }
                            }
                />
                <Dialog open={this.state.dialog_open}>
                    <DialogTitle>Esti sigur ca vrei sa trimiti datele?</DialogTitle>
                            <DialogActions>
                                <Button onClick={this.handleCancelSave.bind(this)}>
                                       NU
                                </Button>
                                <Button disabled={this.state.form_has_been_submitted} onClick={this.handleOkSave.bind(this)}>
                                       DA
                                </Button>
                        </DialogActions>
                </Dialog>
            </div>
        );
    }
}
ReferendumForm.propTypes = {
    classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(withRouter(ReferendumForm));