import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles'
import { TextField, Button, Typography, Snackbar, CircularProgress } from '@material-ui/core';
import AppUrl from './functionals/AppUrl';
import LinkCall from './globals/LinkCall.js'
import CustomAjaxRequest from './functionals/CustomAjaxRequest';
//this.props.history.goBack();
const styles = theme => ({
    container: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    textField: {
        marginTop: 0,
        '& input': {
            padding: '10px 5px',
        },
    },
    button: {
        marginTop: '20px',
        padding: '15px 5px'
    },
    snackError: {
        background: '#f00'
    },
    snackSuccess: {
        background: '#002a59'
    },
    progress: {
        margin: theme.spacing.unit * 2,
      },

});


class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: localStorage.getItem('user') != null ? JSON.parse(localStorage.getItem('user')) : '',
            detalii_judet: localStorage.getItem('detalii_judet') != null ? JSON.parse(localStorage.getItem('detalii_judet')) : '',
            detalii_sectie: localStorage.getItem('detalii_sectie') != null ? JSON.parse(localStorage.getItem('detalii_sectie')) : '',
            fields: {},
            errors: {
                classes: {},
                messages: {}
            },
            openSnack: false,
            snackTheme: 1,
            server_response_message: '',
            focused_fields: [],
            login_success: false
        }
    }
    componentDidMount() {
        if (this.state.user != '' && this.state.detalii_judet != '' && this.state.detalii_sectie != '') {
            this.props.history.push('/homepage')
        }
    }
    handleValidation() {
        var fields = this.state.fields;
        var errors = {
            classes: {},
            messages: {}
        };
        var formIsValid = true;
        if (!fields['login_phone']) {
            formIsValid = false;
            errors['classes']['login_phone_error'] = 'custom_error';
            errors['messages']['login_phone'] = 'Phone missing';
            errors['login_phone'] = true;
        }
        if (!fields['login_pin']) {
            formIsValid = false;
            errors['classes']['login_pin_error'] = 'custom_error';
            errors['messages']['login_pin'] = 'PIN missing';
            errors['login_pin'] = true;
        }
        this.setState({
            errors: errors
        }, function() {});
        return formIsValid;
    }
    ajaxValidation() {
        var self = this;
        var login_phone = this.state.fields.login_phone;
        var login_pin = this.state.fields.login_pin;
        if(this.state.login_success === false){
            this.setState({login_success: true})
            CustomAjaxRequest('phone=' + login_phone + '&pin=' + login_pin, 'POST', global.app_url + 'observer/login', function(r) {
                var res = JSON.parse(r);
                self.setState({
                    openSnack: true,
                })
                if (res.ok === false) {
                    self.setState({
                        server_response_message: res.error_label,
                        snackTheme: 1,
                        login_success: false
                    })
                } else {
                    localStorage.setItem('user', JSON.stringify(res))
                    self.setState({
                        server_response_message: "Login cu succes. Redirect...",
                        snackTheme: 2
                    }, function() {
                        //setTimeout(function() {
                            self.props.history.push('/section_selector')
                            self.setState({
                                login_success: false
                            })
                        //}, 2000)
                    })
                }
            })
        }
    }
    handleChange(field, e) {
        var fields = this.state.fields;
        fields[field] = e.target.value;
        this.setState({
            fields
        });
    }
    handleSubmit(e) {
        e.preventDefault();
        if (this.handleValidation()) {
            //success //redirect to homepage
            this.ajaxValidation();
        } else {
            //alert("Form has errors.")
        }
    }
    snackbarClose() {
        this.setState({ openSnack: false })
    }
    onFocus(field) {
        var focused_fields = this.state.focused_fields;
        focused_fields[field] = true
        this.setState({
            focused_fields
        });
    }
    onBlur(field) {
        var focused_fields = this.state.focused_fields;
        focused_fields[field] = false
        this.setState({
            focused_fields
        });
    }
    render() {
        const { classes } = this.props;
        return ( 
        <div className = 'login' >
            <img src = 'images/login_cover.jpg' />
            <div className = 'page_inner' >
            <Typography variant = "h5"
            gutterBottom>
            Reprezentant USR PLUS </Typography> 
            <form className = { classes.container }
            onSubmit = { this.handleSubmit.bind(this) }
            autoComplete = "off" >
            <TextField error = { this.state.errors.login_phone }
                id = "standard-phone"
                label = "Nr tel, ex. 0722123456"
                className = { classes.textField + " " + (this.state.focused_fields['login_phone'] == true ? ' expanded_input' : '') }
                value = { this.state.login_phone }
                onChange = { this.handleChange.bind(this, 'login_phone') }
                fullWidth onFocus = { this.onFocus.bind(this, 'login_phone') }
                onBlur = { this.onBlur.bind(this, 'login_phone') }
                type='number'
            /> 
            <TextField error = { this.state.errors.login_pin }
                id = "standard-pin"
                autoCapitalize='none'
                label = "COD"
                type = "password"
                className = { classes.textField + " " + (this.state.focused_fields['login_pin'] == true ? ' expanded_input' : '') }
                value = { this.state.password }
                onChange = { this.handleChange.bind(this, 'login_pin') }
                fullWidth onFocus = { this.onFocus.bind(this, 'login_pin') }
                onBlur = { this.onBlur.bind(this, 'login_pin') }
            /> 
            {this.state.login_success &&
                <div className='global_progress_loader'>
                    <CircularProgress className={classes.progress} size={35} />
                </div>
            }
            <Button fullWidth variant = "outlined"
            color = "primary"
            type = "submit"
            className = { classes.button + ' login_submit_button' } >
            INTRÄ‚ </Button> </form>

            </div> 
            <Snackbar className='login_snackbar' open = { this.state.openSnack }
                    anchorOrigin = {
                        {
                            vertical: 'bottom',
                            horizontal: 'left',
                        }
                    }
                    maxSnack = { 2 }
                    autoHideDuration = { 3000 }
                    onClose = { this.snackbarClose.bind(this) }
                    message = { <span className='server_response_message'> { this.state.server_response_message } </span>}
                        ContentProps = {
                            {
                                classes: {
                                    root: this.state.snackTheme === 1 ? classes.snackError : classes.snackSuccess
                                }
                            }
                        }
            />
                <LinkCall />
                 </div>
            );
        }
    }
    Login.propTypes = {
        classes: PropTypes.object.isRequired,
    };


    export default withStyles(styles)(Login);