import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from "react-router-dom";
import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles'
import {CircularProgress, DialogTitle, DialogActions, Dialog, Snackbar, TextField, Button, Typography, List, ListItem, ListItemText, ListItemLink, Divider, Grid, Paper, Select, MenuItem, FormControl, InputLabel  } from '@material-ui/core';
import AppUrl from './functionals/AppUrl';
import CustomAjaxRequest from './functionals/CustomAjaxRequest';
import LinkCall from './globals/LinkCall'

const styles = theme => ({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    button: {
        marginTop: '20px',
        padding: '15px 5px'
    },
    snackError: {
        background: '#f00'
    },
    snackSuccess: {
        background: '#002a59'
    }
});

class SectionSelector extends Component {
    constructor(props){
        super(props);
        this.state = {
            user: localStorage.getItem('user') != null ? JSON.parse(localStorage.getItem('user')) : '',
            detalii_judet: localStorage.getItem('detalii_judet') != null ? JSON.parse(localStorage.getItem('detalii_judet')) : '',
            detalii_sectie: localStorage.getItem('detalii_sectie') != null ? JSON.parse(localStorage.getItem('detalii_sectie')) : '',
            judete: '',
            sectii: false,
            fields: {},
            errors: {
                classes: {},
                messages: {}
            },
            openSnack: false,
            snackTheme: 1,
            server_response_message: '',
            detalii_sectie_aleasa_de_user: '',
            sectie_confirmata: false
        }
    }
    componentDidMount(){
        if (this.state.user != '' && this.state.detalii_judet != '' && this.state.detalii_sectie != '') {
            this.props.history.push('/homepage')
        }else{
            if(this.state.user){
                this.setState({
                    judete: this.state.user.judete,
                }, function(){
                    //console.log(this.state)
                })
            }else{
                this.props.history.push('/')
            }
        }
        
    }
    handleChange(field, e) {
        var fields = this.state.fields;
        fields[field] = e.target.value;
		var errors = {
			classes: {},
			messages: {}
        };
        if(field === 'judet'){
            var sectii_by_judet = this.state.user.sectii.filter(function(sectie){
                return sectie.judet_id === e.target.value;         
            });
            this.setState({
                sectii: sectii_by_judet
            })
        }
        this.setState({
            fields
        });
    }
    handleValidation(){
        var fields = this.state.fields;
        var errors = {
            classes: {},
            messages: {}
        };
        var formIsValid = true;
        if (!fields['judet']) {
            formIsValid = false;
            errors['classes']['judet_error'] = 'custom_error';
            errors['messages']['judet'] = 'Judet missing';
            errors['judet'] = true;
        }
        if (!fields['sectie']) {
            formIsValid = false;
            errors['classes']['sectie_error'] = 'custom_error';
            errors['messages']['sectie'] = 'Sectie missing';
            errors['sectie'] = true;
        }
        return formIsValid;
    }
    handleSubmit(e){
        e.preventDefault();
        var self = this;
        if (this.handleValidation()) {
            //success //redirect to homepage
            //observer/section/select . 
            if(this.state.sectie_confirmata === false){
                this.setState({sectie_confirmata: true})
                CustomAjaxRequest('observer_id=' + this.state.user.id + '&token=' + this.state.user.token + '&section_id=' + this.state.fields.sectie, 'POST', global.app_url + 'observer/section/select', function(r) {
                    var res = JSON.parse(r);
                    self.setState({
                        openSnack: true,
                    })
                    if (res.ok === false) {
                        self.setState({
                            server_response_message: res.errorLabel,
                            snackTheme: 1,
                            sectie_confirmata: false
                        })
                    } else {
                        var judet_selectat = self.state.user.judete.filter(function(judet){
                            return judet.id === self.state.fields.judet;         
                        });
                        var sectie_selectata = self.state.user.sectii.filter(function(sectie){
                            return sectie.id === self.state.fields.sectie;         
                        }); 
                        localStorage.setItem('detalii_sectie', JSON.stringify(sectie_selectata))
                        localStorage.setItem('detalii_judet', JSON.stringify(judet_selectat))
                        self.setState({
                            server_response_message: "SecÈ›ie confirmatÄƒ. Redirect...",
                            snackTheme: 2,
                        }, function() {
                            //setTimeout(function() {
                                self.props.history.push('/homepage')
                                self.setState({
                                    sectie_confirmata: false
                                })
                            //}, 2000)
                        })
                    }
                    //check for error messages and add condition
                })
            }
        } else {
            //console.log('missing fields')
            //alert("Form has errors.")
        }
    }
    snackbarClose() {
        this.setState({ openSnack: false })
    }
    render() {
        const { classes } = this.props;
        var self = this
        return (
            <div className='section_selector'>
                <img src = 'images/login_cover.jpg' />
                <div className='page_inner'>
                    <form className={classes.container} autoComplete="off" onSubmit = { this.handleSubmit.bind(this) }>
                            <FormControl fullWidth className='step2_fields custom_select_field'>
                                <InputLabel htmlFor="judet" className='select_label'>JudeÈ›</InputLabel>
                                <Select
                                    value={this.state.fields.judet || ''}
                                    onChange={this.handleChange.bind(this, 'judet')}
                                    name="judet"
                                    className='custom_select'
                                >
                                {this.state.judete &&
                                    this.state.judete.map((judet) => {
                                        return(
                                            <MenuItem className='custom_select_option' value={judet.id}>{judet.name}</MenuItem>
                                        )
                                    })
                                }
                                </Select>
                            </FormControl>
                            {this.state.sectii &&
                                <FormControl fullWidth className='step2_fields custom_select_field'>
                                    <InputLabel htmlFor="sectie" className='select_label'>SecÈ›ie</InputLabel>
                                    <Select
                                        value={this.state.fields.sectie || ''}
                                        onChange={this.handleChange.bind(this, 'sectie')}
                                        name="sectie"
                                        className='custom_select'
                                    >
                                    {this.state.sectii &&
                                        this.state.sectii.map((sectie) => {
                                            return(
                                                <MenuItem className='custom_select_option' value={sectie.id}><strong>Nr. {sectie.nr}</strong>{' - ' + sectie.name}</MenuItem>
                                            )
                                        })
                                    }
                                    </Select>
                                </FormControl>
                            }
                            {this.state.sectie_confirmata &&
                                <div className='global_progress_loader'>
                                    <CircularProgress className={classes.progress} size={35} />
                                </div>
                            }
                            {this.state.fields['judet'] && this.state.fields['sectie'] ? (
                                <Button fullWidth variant = "outlined"
                                color = "primary"
                                type = "submit"
                                className = { classes.button + " login_submit_button" } >
                                ContinuÄƒ </Button>
                            ) : ( '' )}
                    </form>
                    <LinkCall />

                    <Snackbar open = { this.state.openSnack }
                            anchorOrigin = {
                                {
                                    vertical: 'bottom',
                                    horizontal: 'left',
                                }
                            }
                            maxSnack = { 2 }
                            autoHideDuration = { 3000 }
                            onClose = { this.snackbarClose.bind(this) }
                            message = { <span className='server_response_message'> { this.state.server_response_message } </span>}
                                ContentProps = {
                                    {
                                        classes: {
                                            root: this.state.snackTheme === 1 ? classes.snackError : classes.snackSuccess
                                        }
                                    }
                                }
                    />
                </div>
            </div>
        );
    }
}
SectionSelector.propTypes = {
    classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(SectionSelector);