import React from 'react';
import PropTypes from 'prop-types';
import { Link } from "react-router-dom";
import { withStyles } from '@material-ui/core/styles';
import { IconButton, Grid, Button, Typography, Toolbar, AppBar, Paper, Divider } from '@material-ui/core';
import {MenuIcon, ExitToApp, ArrowBack} from '@material-ui/icons';

const styles = theme => ({
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
  headerRight: {
      textAlign: 'right'
  },
  logo: {
      maxWidth: '70px'
  }
});

class Header extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            observator: this.props.user,
            detalii_judet: localStorage.getItem('detalii_judet') != null ? JSON.parse(localStorage.getItem('detalii_judet')) : '',
            detalii_sectie: localStorage.getItem('detalii_sectie') != null ? JSON.parse(localStorage.getItem('detalii_sectie')) : '',
            pagename: this.props.pagename
        }
        this.isLoggedIn = this.isLoggedIn.bind(this)
        this.getVolunteer = this.getVolunteer.bind(this)
        this.getSelectedSection = this.getSelectedSection.bind(this)
        this.getSelectedCounty = this.getSelectedCounty.bind(this)
    }
    componentDidMount(){
        console.log(this.state)
        if (!this.isLoggedIn()) {
            this.props.history.push('/');
        } 
    }
    isLoggedIn(){
        if (this.getVolunteer() == null) {
            return false;
          }
        if(this.getSelectedSection() == null){
            return false;
        }
        if(this.getSelectedCounty() == null){
            return false;
        }
          //s-ar putea sa aiba cheia expirata;
          return true;
    }
    getVolunteer() {
        return JSON.parse(localStorage.getItem("user"));
    }
    getSelectedSection(){
        return JSON.parse(localStorage.getItem('detalii_sectie'))
    }
    getSelectedCounty(){
        return JSON.parse(localStorage.getItem('detalii_judet'))
    }
    logOut(){
        localStorage.clear();
        this.props.history.push('/')
    }
    render(){
        return (
            <div>
                <div>
                    <AppBar position="static" className='app_bar'>
                        <Toolbar>
                            {this.state.observator ? (
                                <Grid container spacing={0} alignItems="center" justify="center">
                                    <Grid item xs={10} sm={10} className='app_bar_col app_bar_left'>
                                        <img class="app_logo" src="images/logo.png"></img>
                                        {this.state.pagename === 'homepage' ? (
                                            this.state.detalii_sectie &&
                                                <strong>SecÈ›ia {this.state.detalii_sectie[0].name} nr. {this.state.detalii_sectie[0].nr}, {this.state.detalii_judet[0].name}</strong>
                                        ) : (
                                            <strong className='section_title'>{this.state.pagename}</strong>
                                        )}
                                    </Grid>
                                    <Grid item xs={2} sm={2} className='app_bar_col app_bar_right'>
                                        {this.state.pagename === 'homepage' ? (
                                            <ExitToApp className='logout_button' onClick={this.logOut.bind(this)} />
                                        ) : (
                                            <Link className='custom_btn back_btn' to='/homepage'><ArrowBack color='#012A59'/></Link>
                                        )}
                                    </Grid>
                                </Grid>
                            ) : ('')}
                        </Toolbar>
                    </AppBar>
                </div>
                <Divider />
            </div>
        );
    }   
}

Header.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Header);