import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from "react-router-dom";
import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles'
import {Fade, AppBar, Toolbar, DialogTitle, DialogActions, Dialog, Snackbar, TextField, Button, Typography, List, ListItem, ListItemText, ListItemLink, Divider, Grid, Paper, Select, MenuItem, FormControl, InputLabel  } from '@material-ui/core';
import AppUrl from './functionals/AppUrl';
import CustomAjaxRequest from './functionals/CustomAjaxRequest';
import LinkCall from './globals/LinkCall'
import Header from './Header.js'

const styles = theme => ({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    button: {
        marginTop: '20px',
        padding: '15px 5px'
    },
    snackError: {
        background: '#f00'
    },
    snackSuccess: {
        background: '#002a59'
    }
});
function formatReqString(arr, key){
    var formatSendTags = '';
    var and = '';
    for(var i = 0; i < arr.length; i++){
        if(arr.length - 1 == i){
            and = '';
        }else{
            and = '&';
        }
        formatSendTags += key + "[]=" + arr[i] + and;
    }
    return formatSendTags;
}
class Quiz extends Component {
    constructor(props){
        super(props);
        this.state = {
            user: localStorage.getItem('user') != null ? JSON.parse(localStorage.getItem('user')) : '',
            questions: '',
            answers: [{text: 'NU'}, {text: 'DA'}],
            qa: localStorage.getItem('quiz') != null ? JSON.parse(localStorage.getItem('quiz')) : [],
            dialog_open: false,
            quiz_saved_message: false,
            data_saved: false,
            snackTheme: 1
        }
        this.buildAnswerForRequest = this.buildAnswerForRequest.bind(this)
    }
    componentDidMount(){
        this.setState({
            questions: this.state.user.questions
        }, function(){
        })
    }
    handleAnswer(questionId, answerId){
        var qa = this.state.qa;
        var self = this;
        qa = this.upsertQa(qa, questionId, answerId);
        this.setState({qa: qa}, function(){
        });
        localStorage.setItem('quiz', JSON.stringify(qa))
    }
    upsertQa(qa, questionId, answerId) {
        for(var i = 0;i < qa.length;i++) {
            //dar daca id-ul intrebarii din array si id-ul raspunsului din array sunt egale cu id-ul intrebarii curente si id-ul raspunsului curent scoate din array aceasta pereche si returneaza array nou
            if(qa[i].questionId == questionId && qa[i].answerId == answerId){
                var index = qa.indexOf(qa[i]);
                if (index > -1) {
                    qa.splice(i, 1);
                }
                this.setState({answer_unchecked: true})
                return qa;
            }
            //daca id-ul intrebarii din array este egal cu cel al intrebarii curente, inlocuieste cheia answerId,
            if (qa[i].questionId == questionId) {
                qa[i].answerId = answerId;
                return qa;
            }
        }
        //nothing found, append the pair
        this.setState({answer_unchecked: false})
        qa.push({questionId: questionId, answerId: answerId});
        return qa;
    }
    search(nameKey, myArray){
        for (var i=0; i < myArray.length; i++) {
            if (myArray[i].questionId === nameKey) {
                return myArray[i];
            }
        }
        return myArray[i];
    }
    confirmQuizSave(e){
        e.preventDefault();
        this.setState({dialog_open: true})
    }
    handleCancelSave = () => {
        this.setState({
            dialog_open: false
        })
    }
    buildAnswerForRequest() {
        var question_id = [];
        var answer_id = [];
        var qa_local = localStorage.getItem('quiz') != null ? JSON.parse(localStorage.getItem('quiz')) : null;

        if(qa_local != null){
            for (var i = 0;i < qa_local.length;i++) {
                question_id.push(qa_local[i].questionId);
                answer_id.push(qa_local[i].answerId == 0 ? 'nu' : 'da');
            }
            return {question_id: question_id, answer_id: answer_id}
        }else{
            return false;
        }
    }
    handleOkSave = () => {
        //reset quiz ls and call 
        var self = this;
        var qa = this.buildAnswerForRequest()
        var quiz_params = qa ? ("&" + formatReqString(qa.question_id, 'question_id') + "&" + formatReqString(qa.answer_id, 'answer')) : ''
        if(this.state.data_saved === false){
            this.setState({
                data_saved: true
            })
            CustomAjaxRequest('observer_id=' + this.state.user.id + '&token=' + this.state.user.token + quiz_params, 'POST', global.app_url + 'observer/quiz/answer', function(r) { 
                        var res = JSON.parse(r);
                        self.setState({
                            openSnack: true,
                            dialog_open: false,
                        })
                        setTimeout(function(){
                            self.setState({data_saved: false})
                        }, 3000)
                        if(res.ok === false){
                            self.setState({
                                server_response_message: res.errorLabel,
                                snackTheme: 1,
                            })
                        }else{
                            localStorage.removeItem('quiz')
                            localStorage.setItem('quiz_completat', 'da')
                            self.setState({
                                server_response_message: "Date salvate cu succes.",
                                snackTheme: 2,
                                qa: []
                            }, function(){
                                window.scrollTo(0, 0)
                            })
                            
                        }
            })
            
        }
    }
    snackbarClose() {
        this.setState({ openSnack: false })
    }
    render() {
        const { classes } = this.props;
        var self = this
        var state_qa = this.state.qa;
        return (
            <div className='section_selector'>
                <Header user={this.state.user} history={this.props.history} pagename='Chestionar'/>
                <Fade in={true} timeout={1000}>
                    <div className='questions_wrapper page_inner'>
                            {this.state.questions &&
                                this.state.questions.map((question, index) => {
                                    return (
                                        <div className='question_wrap'>
                                            <div className='question' dangerouslySetInnerHTML={{ __html: ((question.position != null) ? (question.position + 1) + '. ' + question.content : '') }}></div>
                                            <Grid container spacing={0}>
                                                {this.state.answers.map((answer, a_index) => {
                                                    var odd_even = (a_index + 1) % 2 == 0 ? 'even_answer' : 'odd_answer'
                                                    var active_answer = state_qa.some(function(obj){return obj.questionId == question.id && obj.answerId == a_index;}) 
                                                    return(
                                                        <Grid onClick={this.handleAnswer.bind(this, question.id, a_index)} item xs={6} className={'q_answer_wrap ' + odd_even + (active_answer === true ? ' active_answer' : '')}>
                                                            <Paper className='q_answer'>
                                                                {answer.text}
                                                            </Paper>
                                                        </Grid>
                                                    )
                                                })}
                                            </Grid>
                                        </div>
                                    )
                                })
                            }
                            <Grid container spacing={0} >
                                <Grid item xs={12} className='center'>
                                {this.state.qa.length > 0 &&
                                    <Link className='custom_btn back_btn link_btn section_save_button' to='' onClick={this.confirmQuizSave.bind(this)}>FINALIZEAZÄ‚</Link>
                                }
                                </Grid>
                            </Grid>
                    </div>
                </Fade>
                <Dialog open={this.state.dialog_open}>
                    <DialogTitle>Esti sigur ca vrei sa trimiti datele?</DialogTitle>
                            <DialogActions>
                                <Button onClick={this.handleCancelSave.bind(this)}>
                                       NU
                                </Button>
                                <Button onClick={this.handleOkSave.bind(this)}>
                                       DA
                                </Button>
                        </DialogActions>
                </Dialog>
                <Snackbar open = { this.state.openSnack }
                        anchorOrigin = {
                            {
                                vertical: 'bottom',
                                horizontal: 'left',
                            }
                        }
                        maxSnack = { 2 }
                        autoHideDuration = { 5000 }
                        onClose = { this.snackbarClose.bind(this) }
                        disableWindowBlurListener={false}
                        message = { <span className='server_response_message'> { this.state.server_response_message } </span>}
                            ContentProps = {
                                {
                                    classes: {
                                        root: this.state.snackTheme === 1 ? classes.snackError : classes.snackSuccess
                                    }
                                }
                            }
                />
            </div>
        );
    }
}
Quiz.propTypes = {
    classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(Quiz);