class Faq extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: localStorage.getItem('user') != null ? JSON.parse(localStorage.getItem('user')) : '',
            expanded: 'panel1',
        }
    }
    componentDidMount() {
    }
    handleChange = panel => (event, expanded) => {
        this.setState({
            expanded: expanded ? panel : false,
        });
    };
    render() {
        const { expanded } = this.state;
        const { classes } = this.props;
        var self = this
        return (
            <div className=''>
                <Header user={this.state.user} history={this.props.history} pagename='GHID'/>
                
                    {ghid_text.map(function(text, index){
                        return (
                            <Fade in={true} timeout={1000}>
                                <ExpansionPanel expanded={expanded === ('panel' + (index + 1))} onChange={self.handleChange('panel' + (index + 1))}>
                                    <ExpansionPanelSummary expandIcon={<ExpandMore />}>
                                        <Typography className={classes.heading + ' ghid_question'}>{text.question}</Typography>
                                    </ExpansionPanelSummary>
                                    <ExpansionPanelDetails>
                                        <Typography className='ghid_text' dangerouslySetInnerHTML={{ __html: text.answer }}></Typography>
                                    </ExpansionPanelDetails>
                                </ExpansionPanel>
                            </Fade>
                        )
                    })}
                
                <div style={{textAlign: 'center', padding: '0 20px'}}>
                    <a className='custom_btn back_btn link_btn section_save_button' href='https://www.usrplus.ro/ghid' target='_blank'>Ghid USR-PLUS</a>
                </div>
            </div>
        );
    }
}
